export default {
	data() {
		return {
			cid:-1,
			scenicInfo:{}
		}
	},
	onLoad(e) {
		this.cid = e.id;
		this.getScenicInfo();
	},
	methods: {
		// 拨打电话功能
		phoneCall(phone){
			uni.makePhoneCall({
				phoneNumber:phone,
				success: (res) => {
					console.log(res);
				},
				fail: (res) => {
					console.log(res);
				}
			})
		},
		getScenicInfo() {
			let that = this;
			that.baseGet( 
				that.U({ c: 'scenic_api', a: 'get_scenic_by_cid',q: {
						cid:that.cid
				}}),
				function(res) {
					that.scenicInfo = res.data
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToMap() {
			let that=this;
			uni.getLocation({
			 type: 'gcj02', //返回可以用于uni.openLocation的经纬度 
				success: function (res) {
					const latitude = res.latitude;
					const longitude = res.longitude;
					uni.openLocation({
						latitude: latitude,
						longitude: longitude,
						success: function () {
							
						},
					});
				}
			})
		},
		
	}
}
